.top-info {
    display: flex;
    flex-shrink: 0;  /* 高さ固定 */
    height: 60px;
    width: 100%;
    gap: 10px;
    margin-top: 8px;
}

.top-info img, .album-art-skeleton {
    margin-left: 16px;
    height: 100%;
    /* border-radius: 14px; */
}

p {
    display: inline;
}

.info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 4px;
}

.title {
    font-size: 24px;
    color: white;
}

.artist{
    color: white;
}

/* アーティスト コンマ 入力 */
.artist::after {
    content: ", ";
}
.artist:last-child::after {
    content: "";
}
